@import url(https://fonts.googleapis.com/css?family=Lato:400,700,900);
body{text-align:center;background-color:#f4f4f4;margin:0;padding:0;font-family:"Lato",sans-serif;color:#fff;overflow-x:hidden;-webkit-transition:0.25s ease-out;transition:0.25s ease-out}h1,h3,p{font-family:"Lato",sans-serif;color:#444444}.contents{margin-top:87px;width:100%}#pokemon_list{margin-top:87px}.about,.mypokemon{margin-top:100px}.HomePage{display:grid;grid-template-columns:repeat(6, auto);grid-template-rows:auto;grid-template-areas:"loader loader loader loader loader loader" "pokemon_list pokemon_list pokemon_list pokemon_list pokemon_list pokemon_list"}.HomePage h3{font-weight:700;font-size:18px}.Search{grid-area:search;box-sizing:border-box;border:2px solid #6e6865;border-radius:50px;font-size:20px;width:200px;height:48px;-webkit-transition:0.5s ease-out;transition:0.5s ease-out}.Search img{position:relative;top:7px;right:10px;width:30px;height:auto;opacity:0.5;-webkit-transition:0.5s ease-out;transition:0.5s ease-out}.Search input{background-color:transparent;border:0;color:#fff;text-align:left;position:relative;bottom:4px;width:110px;outline:none;height:100%;-webkit-appearance:none;-moz-appearance:none;appearance:none}#pokemon_list{grid-area:pokemon_list;margin-bottom:200px;padding-bottom:20px}.grayImg{-webkit-filter:grayscale(100%);filter:grayscale(100%)}#pokeIndex{position:relative;font-size:30px;-webkit-margin-before:15px;margin-block-start:15px;-webkit-margin-after:0px;margin-block-end:0px;bottom:50px}.each_pokemon_button{text-transform:capitalize;font-size:20px;font-weight:bold;width:180px;height:200px;margin:10px;background:#fff;border:1px solid #eee;box-shadow:0px 2px 0.5px #dddddd;-webkit-transition:0.25s ease-out;transition:0.25s ease-out;overflow:hidden}.each_pokemon_button img{position:absolute;margin:0;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);overflow:hidden}.each_pokemon_button:hover{-webkit-filter:grayscale(100%);filter:grayscale(100%);background:#f4f4f4;box-shadow:none;border:none;-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.each_pokemon_button:active{-webkit-filter:grayscale(100%);filter:grayscale(100%);background:#f4f4f4;box-shadow:none;border:none;-webkit-transform:scale(1.1, 1.1);transform:scale(1.1, 1.1)}.nametag{position:relative;top:50px;overflow:hidden}

.loader{position:relative;margin:137px 50px 50px 50px;display:inline-block;grid-area:loader;justify-self:center}.loader span{grid-area:loader;border-radius:50%;display:block;height:20px;width:20px;background:#ddd;position:absolute}.loader span:nth-child(1){left:-35px;-webkit-animation:bounce 1s ease-in-out infinite;animation:bounce 1s ease-in-out infinite}.loader span:nth-child(2){-webkit-animation:bounce 1s ease-in-out 0.13s infinite;animation:bounce 1s ease-in-out 0.13s infinite}.loader span:nth-child(3){left:35px;-webkit-animation:bounce 1s ease-in-out 0.195s infinite;animation:bounce 1s ease-in-out 0.195s infinite}@-webkit-keyframes bounce{0%,75%,100%{-webkit-transform:translateY(0px);transform:translateY(0px)}25%{-webkit-transform:translateY(-35px);transform:translateY(-35px)}}@keyframes bounce{0%,75%,100%{-webkit-transform:translateY(0px);transform:translateY(0px)}25%{-webkit-transform:translateY(-35px);transform:translateY(-35px)}}

.selectedPokemon{position:fixed;bottom:70px;background-color:#fff;border:2px solid #eeeeee;-webkit-transition:0.25s ease-out;transition:0.25s ease-out;-webkit-transform:translateY(100%);transform:translateY(100%);width:100%;height:auto;max-height:300px;overflow-y:auto}.capitalize{text-transform:capitalize}#pokemonTypeButton{border:0;cursor:pointer;display:flex;width:100%;outline:0;align-items:center;justify-content:center;height:70px}#pokemonTypeButton p{font-size:16px}hr{width:90%;border-bottom:1px solid #d6d6d6}#pokeData{text-transform:capitalize}#pokeData img{max-width:250px;min-width:150px;width:100%;height:auto}#pokeDataStats{padding-top:25px}#weaknessAdvantage{display:flex;align-items:start;justify-content:center}#lists{text-transform:capitalize;background-color:inherit}table{font-family:arial, sans-serif;border-collapse:collapse;width:100%;color:black}td,th{border:1px solid #dddddd;text-align:left;padding:8px}tr:nth-child(even){background-color:#dddddd}

body{color:#444444}.submitButton{border-radius:50%;background-color:#57afff;display:block;border-style:none;height:25px;width:25px;text-align:center;color:#fff;font-weight:bold;font-size:15px;outline-style:none;-webkit-transition:all .3s ease-out;transition:all .3s ease-out}.submitButton:hover{color:#dedede}.submitButton:active{color:#DE5F8C}

